.c-btn-arrow {
  width: 100%;
  align-content: center;
  color: #092533;
  border: 1px solid #092533;
  position: relative;
  display: inline-block;
  border-radius: 1.875rem;
}

.c-btn-arrow:hover {
  opacity: initial;
}

.c-btn-arrow::after {
  content: "";
  background-image: url(../../assets/images/common/more-arrow.svg);
  transition: all 0.2s;
  background-position: 90% 50%; /* 重複しているこの行を削除 */
  background-repeat: no-repeat;
  display: inline-block;
  background-size: contain;
  width: 1.75rem;
  height: 0.9375rem;
  color: #092533;
  position: absolute;
  top: 50%;
  right: 5%;
  transform: translateY(-50%);
}

.c-btn-arrow:hover::after {
  right: -7%;
}

.c-btn-slide {
  background-repeat: no-repeat;
  background-size: cover;
  width: 100%;
  align-content: center;
  color: #092533;
  border: 1px solid #092533;
  opacity: 1;
  position: relative;
  display: inline-block;
}

.--small {
  max-width: 11.25rem;
  padding: 0.5em 0.8em;
}

.--big {
  max-width: 20rem;
  padding: 0.8em 0.8em;
}

.c-btn-slide::before {
  content: "";
  width: 0;
  height: 100%;
  display: block;
  background: #092533;
  position: absolute;
  left: 0;
  top: 0;
  transition: all 0.2s;
  z-index: -1;
}

.c-btn-slide::after {
  content: "";
  background-image: url(../../assets/images/common/more-arrow.svg);
  transition: all 0.2s;
  background-position: 90% 50%; /* 重複しているこの行を削除 */
  background-repeat: no-repeat;
  display: inline-block;
  background-size: contain;
  width: 1.75rem;
  height: 0.9375rem;
  color: #092533;
  position: absolute;
  top: 50%;
  right: 6%;
  transform: translateY(-50%);
}

.c-btn-slide:hover {
  color: #092533;
}
@media screen and (min-width: 768px) {
  .c-btn-slide:hover {
    color: #fff;
    opacity: initial;
  }
}

@media screen and (min-width: 768px) {
  .c-btn-slide:hover::before {
    width: 100%;
    color: #fff;
  }
}

@media screen and (min-width: 768px) {
  .c-btn-slide:hover::after {
    background-image: url(../../assets/images/common/more-arrow-white.svg);
  }
}

