  display: inline-block;
  background-size: contain;
  width: 14px;
  height: 14px;
}

.c-drower__item a:hover {
  text-decoration: underline red;
}

.c-drower.open {
  opacity: 1;
  visibility: visible;
  transform: translateX(0%);
}

