/* 問い合わせステップ */
.box_step03 {
  width: 70%;
  margin: 0 auto;
}

.box_step03 ul {
  position: relative;
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: space-between;
  justify-content: space-between;
  padding-left: 0;
}

.box_step03 ul li {
  position: relative;
  color: #333;
  z-index: 1;
}

.box_step03 ul li::marker {
  color: #fff;
}

.box_step03 ul li:before {
  content: "";
  display: block;
  width: 15px;
  height: 15px;
  background: #ddd;
  margin: 0 auto 0.5em;
  -moz-border-radius: 100%;
  -webkit-border-radius: 100%;
  border-radius: 100%;
}

.box_step03 ul li.active {
  font-size: 16px;
  color: #333;
}

.box_step03 ul li.active:before {
  margin: -0.3em auto 0.5em;
  background: #2888a3;
  width: 25px;
  height: 25px;
}

.box_step03 ul:before {
  position: absolute;
  z-index: 1;
  width: 90%;
  height: 1px;
  background: #ddd;
  content: "";
  left: 50%;
  top: 7px;
  z-index: 0;
  -moz-transform: translateX(-50%);
  -ms-transform: translateX(-50%);
  -webkit-transform: translateX(-50%);
  transform: translateX(-50%);
}

@media only screen and (max-width: 767px) {
  .box_step03 {
    width: 90%;
  }
}
@media only screen and (max-width: 480px) {
  .box_step03 ul:before {
    width: 85%;
  }
}
